<template>

    <div class="notifications-dropdown-content">

        <div class="notifications">

            <div class="h6"></div>

            <div v-if="notifications.noread !== undefined ? ( notifications.noread.length !== 0 ? false : true ) : false"
                 class="notifications-empty">  
            </div>

            <div v-else
                 class="notifications-list">
                <div v-for="notification in notifications.noread"
                     :key="notification.id"
                     class="notification-item new"
                     @mouseenter="readNotification(notification.id)">
                    <template v-if="notification.type === 1">
                        <div class="notification-title">{{getStatusText(notification).title}}</div>
                        <div class="notification-link">
                            <a :href="getStatusText(notification).link.url">{{getStatusText(notification).link.title}}</a>
                        </div>
                    </template>
                    <template v-else-if="notification.type === 2">
                        <div class="notification-title danger"> {{notification.items.order_id}}</div>
                        <p>{{notification.items.text}}</p>
                    </template>
                    <template v-else-if="notification.type === 3">
                        <div class="notification-title">{{notification.name}}</div>
                        <p>{{notification.items.text}}</p>
                    </template>
                    <div class="notification-date">{{parseDate(notification.date)}}</div>
                </div>
            </div>

        </div>

        <div class="notifications">

            <div class="h6"></div>

            <div v-if="notifications.read !== undefined ? ( notifications.read.length !== 0 ? false : true ) : false"
                 class="notifications-empty">  
            </div>

            <div v-else
                 class="notifications-list">
                <div v-for="notification in notifications.read"
                     :key="notification.id"
                     class="notification-item">
                    <template v-if="notification.type === 1">
                        <div class="notification-title">{{getStatusText(notification).title}}</div>
                        <div class="notification-link">
                            <a :href="getStatusText(notification).link.url">{{getStatusText(notification).link.title}}</a>
                        </div>
                    </template>
                    <template v-else-if="notification.type === 2">
                        <div class="notification-title danger"> {{notification.items.order_id}} - </div>
                        <p>{{notification.items.text}}</p>
                    </template>
                    <template v-else-if="notification.type === 3">
                        <div class="notification-title">{{notification.name}}</div>
                        <p>{{notification.items.text}}</p>
                    </template>
                    <div class="notification-date">{{parseDate(notification.date)}}</div>
                </div>
            </div>

        </div>

    </div>

</template>

<script>

    import {mapState} from 'vuex'
    import moment from 'moment'

    export default {
        name: "Notifications",
        computed: {
            ...mapState({
                notifications: state => state.notifications.notifications
            })
        },
        methods: {
            getStatusText(item) {
                let status = item.items.status
                switch (status) {
                    case 1:
                        return {
                            title: ` ${item.name} -  `,
                            link: {
                                title: '  ',
                                url: `/orders`
                            }
                        }
                    case 2:
                        return {
                            title: ` ${item.name} -   `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 3:
                        return {
                            title: ` ${item.name} -    `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 4:
                        return {
                            title: ` ${item.name} -   `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 5:
                        return {
                            title: ` ${item.name} -    `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 6:
                        return {
                            title: ` ${item.name} -  `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 7:
                        return {
                            title: ` ${item.name} - `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 8:
                        return {
                            title: ` ${item.name} -   `,
                            link: {
                                title: ' ',
                                url: `/load-documents/${item.items.order_id}`
                            }
                        }
                    case 9:
                        return {
                            title: ` ${item.name} - `,
                            link: {
                                title: '  ',
                                url: `/orders`
                            }
                        }
                    case 10:
                        return {
                            title: ` ${item.name} -  `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 11:
                        return {
                            title: ` ${item.name} -   `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 12:
                        return {
                            title: ` ${item.name} -    `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 13:
                        return {
                            title: ` ${item.name} -  `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 14:
                        return {
                            title: ` ${item.name} - `,
                            link: {
                                title: ' ',
                                url: `/signing-documents/${item.items.order_id}`
                            }
                        }
                    case 15:
                        return {
                            title: ` ${item.name} -   `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 16:
                        return {
                            title: ` ${item.name} -    `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 17:
                        return {
                            title: ` ${item.name} -   `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 18:
                        return {
                            title: ` ${item.name} -    `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 19:
                        return {
                            title: ` ${item.name} -  `,
                            link: {
                                title: ' ',
                                url: `/orders/${item.items.order_id}`
                            }
                        }
                    case 20:
                        return {
                            title: ` ${item.name} -  `,
                            link: {
                                title: '   ',
                                url: `/received-loans/${item.items.order_id}`
                            }
                        }
                }
            },
            parseDate(date) {
                let dateMoment = moment(date).locale('ru'),
                    currDate = moment(new Date()),
                    returnDate = dateMoment.format('DD MMMM');
                if ( currDate.year() > dateMoment.year() ) {
                    returnDate = dateMoment.format('DD MMMM YYYY') + ' '
                }
                if ( currDate.day() === dateMoment.day() ) {
                    returnDate = `  ${dateMoment.format('HH:mm')}`
                }
                return returnDate
            },
            readNotification(id) {
                this.$store.dispatch('readNotification', id)
            }
        }
    }
</script>

