//
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueMask from 'v-mask'

import Vuelidate from 'vuelidate'
import vSelect from 'vue-select'
import VueCookies from 'vue-cookies'
import axios from 'axios'
import cookie from 'vue-cookies'

//   
import { VueMaskFilter } from 'v-mask'
import { i18n, loadLanguageAsync } from "./plugins/i18n"
import GlobalComponents from './globalComponents'
import phoneFilter from './filters/phoneFilter'
import numberFilter from './filters/numberFilter'
import declensionNumbers from './filters/declensionNumbers'

// 
import './assets/scripts/custom.js'

// 

Vue.config.productionTip = false
Vue.prototype.axios = axios

axios.defaults.withCredentials = true
axios.defaults.baseURL = 'https://xn--d1abkftnrcqv.xn--p1ai/api/'
axios.defaults.headers.accept = 'application/json'

Vue.use(VueMask);
Vue.use(Vuelidate);
Vue.use(vSelect);
Vue.use(VueCookies);
Vue.use(GlobalComponents);

Vue.filter('phoneFilter', phoneFilter);
Vue.filter('numberFilter', numberFilter);
Vue.filter('declensionNumbers', declensionNumbers);
Vue.filter('VMask', VueMaskFilter)

// 
Vue.directive('scroll', {
    inserted: function (el, binding) {
        let f = function (evt) {
            if (binding.value(evt, el)) {
                window.removeEventListener('scroll', f)
            }
        }
        window.addEventListener('scroll', f)
    }
})

import './assets/sass/main.sass'

Vue.component('v-select', vSelect)

loadLanguageAsync()

// TODO:   build:prod
if (process.env.NODE_ENV === 'development') {
    const token = cookie.get('token')
    if (token) {
        axios.defaults.headers.common = {'Authorization': `Bearer ${token}`}
    }
}

new Vue({
    router,
    store,
    i18n,
    render: h => h(App)
}).$mount('#app')
