const style = 'body { font-family: DejaVu Sans }table { width: 100%;caption-side:bottom;border-collapse:collapse }table [colspan=\'3\']{background-color:#eee}table .td,table .th,table td,table th{padding:8px 10px;font-size:12px;border:1px solid #ccc;vertical-align:middle}.table-nested{display:table;border-collapse: collapse;}.tr{display:table-row}.td{display:table-cell}.checkbox{display:table;border-collapse:collapse;margin-bottom:10px}.checkbox:last-child{margin-bottom:0}.checkbox-tr{display:table-row}.checkbox-td{vertical-align:middle;display:table-cell}.checkbox-td input{margin-right:10px}.checkbox-td:first-child {width: 20px;}'

let state = {
    finalDocsList: [],
    finalForm: '<html><head><style>' + style + '</style></head><body>' + table + '</body></html>'
}

let mutations = {
    setFinalDocsList(state, list) {
        state.finalDocsList = list
    }
}

let actions = {
    async getFinalDocsList(ctx, id) {
        await axios({
            url: `order/${id}/create-questionnaire`,
            method: 'get'
        }).then(resp => {
            if ( resp.data.status ) {
                let files = resp.data.items
                ctx.commit('setFinalDocsList', files)
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async sendForm(ctx) {
        await axios({
            url: 'pdf',
            method: 'post',
            responseType: 'blob',
            data: {
                html: ctx.state.finalForm
            }
        }).then(resp => {
            fileDownload(resp.data, 'filename.pdf')
        })
    },
    orderResult(ctx, id) {
        return new Promise((resolve, reject) => {
            axios({
                url: `order/${id}/send`,
                method: 'get'
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve(true)
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    resolve(false)
                }
            }).catch(reject)
        })
    },
    orderResultFinish(ctx, id) {
        return new Promise((resolve, reject) => {
            axios({
                url: `order/${id}/send-contracts`,
                method: 'get'
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve(true)
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    resolve(false)
                }
            }).catch(reject)
        })
    },
    async rejectOrder(ctx, id) {
        await axios({
            url: `order/${id}/accord`,
            method: 'post',
            data: {
                accord: false
            }
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.dispatch('getOrders')
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async confirmOrder(ctx, id) {
        await axios({
            url: `order/${id}/accord`,
            method: 'post',
            data: {
                accord: true
            }
        }).then(resp => {
            if ( resp.data.status ) {
                ctx.dispatch('getOrders')
            }
            else {
                ctx.dispatch('getCodeText', resp.data.code)
            }
        })
    },
    async sendAdditionalDocs(ctx, id) {
        return new Promise((resolve, reject) => {
            axios({
                url: `order/${id}/send-other-doc`,
                method: 'get'
            }).then(resp => {
                if ( resp.data.status ) {
                    resolve(true)
                }
                else {
                    ctx.dispatch('getCodeText', resp.data.code)
                    resolve(false)
                }
            }).catch(reject)
        })
    }
}

export default {
    state,
    mutations,
    actions
}
