import axios from "axios";

let state = () => ({
    errorNoTitle: false,
    errorCodeText: '',
    errorModal: false
})

let actions = {
    async getCodeText(ctx, code) {
        await axios({
            method: 'get',
            url: 'https://xn--d1abkftnrcqv.xn--p1ai/content/codes'
        }).then(resp => {
            ctx.commit('updateCode', {code, text: resp.data[code]})
        })
    },
}

let mutations = {
    updateCode(state, code) {
        if (code.code === 110) {
            state.errorNoTitle = true
            state.errorCodeText = ' ,      '
            state.errorModal = true
        } else {
            state.errorNoTitle = false
            state.errorCodeText = code.text
            state.errorModal = true
        }
    },
    closeError(state) {
        state.errorModal = false
    }
}

export default {
    state,
    actions,
    mutations
}
